@font-face {
  font-family: 'robotoregular';
  src: url('../fonts/roboto/roboto-regular-webfont.eot');
  src:
    url('../fonts/roboto/roboto-regular-webfont.eot?#iefix') format('embedded-opentype'),
    url('../fonts/roboto/roboto-regular-webfont.woff2') format('woff2'),
    url('../fonts/roboto/roboto-regular-webfont.woff') format('woff'),
    url('../fonts/roboto/roboto-regular-webfont.ttf') format('truetype'),
    url('../fonts/roboto/roboto-regular-webfont.svg#robotoregular') format('svg');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'robotomedium';
  src: url('../fonts/roboto/Roboto-Medium-webfont.eot');
  src:
    url('../fonts/roboto/Roboto-Medium-webfont.eot?#iefix') format('embedded-opentype'),
    url('../fonts/roboto/Roboto-Medium-webfont.woff') format('woff'),
    url('../fonts/roboto/Roboto-Medium-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'robotobold';
  src: url('../fonts/roboto/Roboto-Bold-webfont.eot');
  src:
    url('../fonts/roboto/Roboto-Bold-webfont.eot?#iefix') format('embedded-opentype'),
    url('../fonts/roboto/Roboto-Bold-webfont.woff') format('woff'),
    url('../fonts/roboto/Roboto-Bold-webfont.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.header {
    font-family: robotoregular;
}

.currentState {
    font-family: robotomedium;
    color: #1890ff;
    margin-bottom: 10px;
    margin-left: 5px;
}

.currentStateError {
    font-family: robotomedium;
    color: #ff4d4f;
    margin-bottom: 10px;
    margin-left: 5px;
}

.fetchButton, .resetButton, .tryAgainButton {
    font-family: robotomedium;
    background-color: #1890ff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
    margin-top: 10px;
    margin-left: 5px;
}

.tryAgainButton {
    background-color: #ff4d4f;
}

.countryTable {
    width: 500px;
    border-collapse: collapse;
    margin-left: 5px;
}

.countryTable th, .countryTable td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.countryTable th {
    background-color: #f2f2f2;
}

.countryTable img {
    vertical-align: middle;
}

.dataLoading, .dataIdle, .dataError {
    width: 500px;
    height: 509px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #fff;
    border-radius: 10px;
    border: 4px solid;
    font-family: robotomedium;
    color: #1890ff;
    
}

.dataMessage {
      width: 400px;
      height: 50px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      border-radius: 10px;
      border: 4px solid;
}

.callBackPanel {
      width: 400px;
      height: 170px;
      display: flex;
      justify-content: center;
      align-items: center;
      background-color: #fff;
      border-radius: 10px;
      border: 4px solid;
      margin-top: 100px;
}

.dataLoading, .dataIdle {
	  border-color: #1890ff;
}

.dataError {
	border-color: #ff4d4f;
}

.dataErrorIcon {
	  width: 70px;
}

.spinner {
    border: 8px solid #f3f3f3;
    border-top: 8px solid #3498db;
    border-radius: 50%;
    width: 64px;
    height: 64px;
    animation: spin 1s linear infinite;
}

.toggleAPI {
    background-color: #fff;
    color: #1890ff;
    font-family: robotomedium;
    border: 3px solid;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    margin-right: 10px;
    margin-top: 10px;
    margin-left: 5px;
}

.fsm-diagram {
    width: 348px;
    height: 287px;
    background-image: url('../img/FSM.jpg');
    background-size: cover;
    background-position: center;
    position: relative;
    top: 34px;
}


@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}
